import { CustomVolSvg } from "../../Style/CustomBarStyled";
import {
  CustomLabel,
  CustomVolBtn,
  CustomVolControler,
  CustomVolInput,
  InputWrapper,
  LabelWrapper,
} from "../../Style/CustomVolStyled";
import { AnimatePresence } from "framer-motion";
import { useState, ChangeEvent } from "react";
import { MusicVolInput } from "../../Style/MainBarStyle";

function CustomVol() {
  const [showControls, setShowControls] = useState(false);
  const handleCustomVolClick = () => {
    setShowControls((pre) => !pre);
  };
  return (
    <>
      <CustomVolBtn onClick={handleCustomVolClick}>
        <CustomVolSvg
          xmlns="http://www.w3.org/2000/svg"
          width="19"
          height="17"
          viewBox="0 0 19 17"
        >
          <g
            id="_280"
            data-name=" 280"
            transform="translate(-1735.643 -160.912)"
          >
            <rect
              id="_267"
              data-name=" 267"
              width="5"
              height="3"
              transform="translate(1735.643 167.912)"
              fill="#fff"
            />
            <rect
              id="_272"
              data-name=" 272"
              width="5"
              height="3"
              transform="translate(1742.643 167.912)"
              fill="#fff"
            />
            <rect
              id="_273"
              data-name=" 273"
              width="5"
              height="2"
              transform="translate(1742.643 164.912)"
              fill="#fff"
            />
            <rect
              id="_274"
              data-name=" 274"
              width="5"
              height="2"
              transform="translate(1742.643 160.912)"
              fill="#fff"
            />
            <rect
              id="_268"
              data-name=" 268"
              width="5"
              height="2"
              transform="translate(1735.643 171.912)"
              fill="#fff"
            />
            <rect
              id="_271"
              data-name=" 271"
              width="5"
              height="2"
              transform="translate(1742.643 171.912)"
              fill="#fff"
            />
            <rect
              id="_275"
              data-name=" 275"
              width="5"
              height="2"
              transform="translate(1749.643 171.912)"
              fill="#fff"
            />
            <rect
              id="_269"
              data-name=" 269"
              width="5"
              height="2"
              transform="translate(1735.643 175.912)"
              fill="#fff"
            />
            <rect
              id="_270"
              data-name=" 270"
              width="5"
              height="2"
              transform="translate(1742.643 175.912)"
              fill="#fff"
            />
            <rect
              id="_276"
              data-name=" 276"
              width="5"
              height="2"
              transform="translate(1749.643 175.912)"
              fill="#fff"
            />
          </g>
        </CustomVolSvg>
      </CustomVolBtn>
      <AnimatePresence>
        {showControls && (
          <CustomVolControler
            initial={{ opacity: 0, x: -300 }}
            animate={{ opacity: 1, x: -307 }}
            exit={{ opacity: 0, x: -300 }}
            transition={{ duration: 0.5 }}
          >
            <InputWrapper>
              <LabelWrapper>
                <CustomLabel htmlFor="cityRain">City Rain</CustomLabel>
                <CustomVolInput
                  type="range"
                  min="0"
                  max="1"
                  step="0.01"
                  value={0.2}
                  id="cityRain"
                  //   onChange={handleVolumeChange}
                />
              </LabelWrapper>
              <LabelWrapper>
                <CustomLabel htmlFor="cagePeople">Cage People</CustomLabel>
                <CustomVolInput
                  type="range"
                  min="0"
                  max="1"
                  step="0.01"
                  value={0.5}
                  id="cagePeople"
                  //   onChange={handleVolumeChange}
                />
              </LabelWrapper>
              <LabelWrapper>
                <CustomLabel htmlFor="cityTraffic">City Traffic</CustomLabel>
                <CustomVolInput
                  type="range"
                  min="0"
                  max="1"
                  step="0.01"
                  value={0.7}
                  id="cityTraffic"
                  //   onChange={handleVolumeChange}
                />
              </LabelWrapper>
            </InputWrapper>
          </CustomVolControler>
        )}
      </AnimatePresence>
    </>
  );
}

export default CustomVol;
